export async function getStaticPaths() {
    prebuildData.loadSnapshot();

    return {
        paths: Object.keys(prebuildData.getCollectionTable()).filter((handle) => handle!=="help").map((id) => ({ params: { id } })),
        fallback: false
    };
}
  
export async function getStaticProps(context) {
    return {
        props: {},
    };
}

export default function Collection() {    
    const reactRouter = useRouter();
    let { id } = reactRouter.query as {[key: string]: string};
    id = id.split("?")[0];

    const { page,mobileDetect: { isActive: isMobile } } = useContext(LayoutStateContext);

    const lastIsMobileRef = useRef(isMobile.current);

    useEffect(() => {
        if (isMobile.current!==lastIsMobileRef.current) {
            lastIsMobileRef.current = isMobile.current;
            page.scrollTo({top: 0,behavior: "smooth"});
        }
    },[isMobile.current]);

    return <CollectionContent key={isMobile.current?1:0} id={id} />;
}

